body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 14px; }
  body footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #222;
    color: white; }
  body a {
    color: #4993a7;
    text-decoration: none; }

html, body, #root, .App {
  height: 100%;
  overflow-y: hidden; }

#body {
  height: 90%; }

footer {
  height: 10%; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40%; }

.App-header {
  background-color: gray;
  height: 20%;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

.stage {
  height: 80%;
  display: -ms-flexbox;
  display: flex; }

input {
  width: 400px;
  height: 30px;
  padding-left: 10px;
  border-radius: 4px;
  border: 1px solid #bab2b2;
  -webkit-box-shadow: 0px 0px 12px #EFEFEF;
          box-shadow: 0px 0px 12px #EFEFEF; }

.icon {
  font-size: -webkit-xxx-large;
  position: absolute;
  display: none; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@media (max-width: 640px) {
  .App-header {
    font-size: xx-small; }
  input {
    width: 80%; }
  .icon {
    display: block; }
  .topnav {
    display: none; }
  .responsive {
    display: block; } }



